import { my_question_list } from '../../api/api.js'
export default {
	data() {
		return {
			access_token:'',
			member_id:'',
			tabCurrentIndex:0,
			type: 1,
			navList: [
				{
					state: 0,
					text: '指定提问'
				},
				{
					state: 1,
					text: '普通提问'
				}
			],
			questionList: []
		}
	},
	onLoad() {
		let that = this
		uni.getStorage({
		    key: 'access_token',
		    success: function (res) {
		        that.access_token = res.data
		    }
		});
		uni.getStorage({
		    key: 'member_id',
		    success: function (res) {
		        that.member_id = res.data
		    }
		});
		console.log(that.member_id)
		console.log(that.access_token)
		that.getMyQuestion(that.type)
	},
	methods: {
		// tab切换
		tabClick(index){
			let that = this
			that.tabCurrentIndex = index
			if(index == 0){
				that.type = 1 //指定提问
			}else if(index == 1){
				that.type = 2 //普通提问
			}
			that.getMyQuestion(that.type)
		},
		//问题列表
		getMyQuestion(type){
			let that = this
			my_question_list({
				member_id: that.member_id,
				access_token: that.access_token,
				type: type
			}).then(res => {
				console.log(res)
				if(res.data.code == 1){
					that.questionList = res.data.data.list
				}
			})
		}
	}
}
